@import '../variables';


.hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    

    width: 100vw;
    margin-top: 10vh;

    .quote {

        color: $darkest;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;

        .quote-text {
            font-style: italic;
            font-size: 1.5rem;
        }
        .quote-author {
            font-size: 1.8rem;
            font-weight: bold;
        }

        margin-bottom: 15vh;
    }


    .coin-feed {
        padding: 5vh 0 15vh 0 ;
        display: grid;
        grid-template-rows: repeat(7, 5vh);
        justify-content: center;
        row-gap: 5vh;
        background-color: $light;
        

        .coin-container{
            display: grid;
            grid-template-columns: repeat(5, 7vw);
            column-gap: 5vw;
            align-items: center;
            justify-content: center;
            text-align: center;





            .up{
                color: darken($up, 20%);
            }
            .down {
                color: darken($down, 20%);
            }
        }


    }



}